import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import favorites from "./favorites.vue"
import BkTabs from "@/bkcomponents/bk-tabs.vue"

export default {
	data() {
		return {
			listHeight: "",
			loadMoreStatus: "more",
			tabList:[
				{id:"1",name:"专栏",isshow:true},
				{id:"2",name:"说说",isshow:true},
				{id:"3",name:"收藏",isshow:true},
				{id:"4",name:"其他",isshow:true},
			],
		}
	},
	onLoad() {
		this.intitHeight();
	},
	components: {
		uniListItem,favorites,BkTabs
	},
	methods: {
		//
		intitHeight(){
			this.listHeight = "600rpx";
		},
		// 打开path页面
		open(path){
			let p = `/pages/${path}`;
			console.log(p)
			uni.navigateTo({
				url: p
			})
		},
		//
		changeTab(e){
			this.loadMoreStatus = "more";
			console.log("切换:"+JSON.stringify(e));
		},
		loadMore(){		
			this.loadMoreStatus = "loading";
			setTimeout(()=>{
				console.log("加载数据完成");
				this.loadMoreStatus = "noMore";
			},3000);
		}
	}
}
